import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import NotFound from '@/components/NotFound'
import Home from '@/components/home'
import Movie from '@/components/movie/index'
export default new Router({
  mode: 'history',
  routes: [
    { // 404
      path: '*',
      component: NotFound
    },
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/movie',
      name: 'movie',
      component: Movie
    }
  ]
})



